import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import elementSo from './views/nav2/elementSo.vue'
import Page5 from './views/nav2/Page5.vue'
import Management from './views/charts/Management.vue'
import CustomerSelect from './views/CustomerSelect.vue'
import c3 from './views/charts/c3.vue'
import echarts from './views/charts/echarts.vue'
import highcharts from './views/charts/highcharts.vue'
import Customer from './views/charts/customers.vue'
import Asset from './views/charts/customersChart.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: 'Management Overview',
        iconCls: 'fa fa-address-card',
        leaf: true,//只有一个节点
        children: [
            { path: '/', component: Management, name: 'Management Overview' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'Customer Overview',
        iconCls: 'el-icon-date',
        leaf: true,//只有一个节点
        children: [
            { path: '/customer-overview', component: Customer, name: 'Customer Overview' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'Asset Overview',
        iconCls: 'el-icon-news',
        leaf: true,//只有一个节点
        children: [
            { path: '/asset-overview', component: Asset, name: 'Asset Overview' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'xxx Overview',
        iconCls: 'fa fa-bar-chart',//图标样式class
        leaf: true,//只有一个节点
        children: [
            { path: '/xx', component: Customer, name: 'xxx Overview', hidden: true }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


